import React from 'react';
import styled from 'styled-components';

const EyeRight = styled.div`
  position: absolute;
  top: 3.5rem;
  left: 11rem;
  width: 0.7rem;
  height: 0.7rem;
  background: #000;
  border-radius: 100%;
`;

const EyeLeft = styled(EyeRight)`
  left: 8rem;
`;

const App: React.FC = () => {
  return (
    <>
      <EyeRight />
      <EyeLeft />
    </>
  );
};
export default App;
