import React, { useContext, useEffect } from 'react';
import AppContext from 'App/context';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import {
  FlexDiv,
  TransBtn,
  BtnImg,
  ColorTxt,
  MonitorView,
  RightBtnDiv,
} from 'components/EtcStyles';
import Background from 'assets/images/background.jpg';
import O from 'assets/images/O.png';
import X from 'assets/images/X.png';
import MonitorLeg from 'assets/images/monitor2.png';
import Combination from 'components/BacteriaCombination';
import { Bacteria } from 'App/datas';
import Button from 'components/Button';
import usePrevious from 'hooks/usePrevious';

const getLevel = (gage: number) => {
  let level = 0;
  if (gage >= 50) {
    level = 5;
  } else if (gage >= 25) {
    level = 4;
  } else if (gage >= 20) {
    level = 3;
  } else if (gage >= 10) {
    level = 2;
  } else if (gage >= 5) {
    level = 1;
  }
  return level;
};

const Page: React.FC = () => {
  const {
    state: { bacterias, modelsSecond },
    generateBacterias,
    goToNext,
    learnNoxiousBacteria,
    learnInnoxiousBacteria,
  } = useContext(AppContext);

  const gage =
    modelsSecond.noxious.learnCount + modelsSecond.innoxious.learnCount;
  const level = getLevel(gage);
  const prevLevel = usePrevious<number>(level) || 0;
  const bacteria = bacterias[gage];

  useEffect(() => {
    if (bacterias.length === 0) {
      generateBacterias(50);
    }
    if (level > prevLevel) {
      // eslint-disable-next-line no-alert
      window.alert(`Level Up ${level}`);
    }
  }, [bacterias, generateBacterias, level, prevLevel]);

  const handleChange = (learn: (_: Bacteria) => void) => () => {
    if (gage < bacterias.length) {
      learn(bacteria);
    }
  };
  const handleChangeO = handleChange(learnNoxiousBacteria);
  const handleChangeX = handleChange(learnInnoxiousBacteria);

  console.log(modelsSecond);

  return (
    <BasePage>
      <Container style={{ backgroundImage: `url(${Background})` }}>
        <DivBase>
          <ContentTitle>AI 2차 학습</ContentTitle>
          <ExplainBox>
            이처럼 AI는 네가 알려준 대로만 행동해. <br />
            이제 네가 만든 AI는 세균인지 아닌지 알 수 있게되었어. <br />
            추가로 AI에게 세균이 유해한지 아닌지를 파악하여 이불 속 유해균을
            없앨 수 있도록 가르쳐보자.
          </ExplainBox>
          <Content style={{ height: 'calc(100% - 2rem)' }}>
            <FlexDiv>
              <TransBtn onClick={handleChangeO}>
                <BtnImg src={O} alt="" />
                <ColorTxt color="#22a415">유해균</ColorTxt>
              </TransBtn>

              <div>
                <div>
                  Battery: {gage} / {bacterias.length}
                </div>
                <MonitorView>
                  <Combination type={bacteria} />
                </MonitorView>
                <img src={MonitorLeg} alt="" />
              </div>

              <TransBtn
                onClick={handleChangeX}
                style={{ marginBottom: '7.8rem' }}
              >
                <BtnImg src={X} alt="" />
                <ColorTxt color="#bf2d2d">무해균</ColorTxt>
              </TransBtn>
            </FlexDiv>
          </Content>
        </DivBase>
        <RightBtnDiv>
          <Button
            onClick={() => {
              goToNext();
              generateBacterias(0);
            }}
            disabled={level < 1}
          >
            학습완료
          </Button>
        </RightBtnDiv>
      </Container>
    </BasePage>
  );
};

export default Page;
