import React from 'react';
import styled from 'styled-components';

const Eye = styled.div`
  border: 0 solid transparent;
  border-top: 5px solid black;
  border-radius: 10rem;
  width: 3rem;
  height: 5rem;
  transition: all 0.1s linear;
  position: absolute;
  top: 2.5rem;
  left: 6rem;

  &::before {
    position: absolute;
    content: '';
    height: 1.8rem;
    width: 1.5rem;
    top: 0.5rem;
    border-radius: 100%;
    display: block;
    background-color: #000;
    left: 0.8rem;
  }
`;
const Eye2 = styled(Eye)`
  left: 10.5rem;
`;

const App: React.FC = () => {
  return (
    <>
      <Eye />
      <Eye2 />
    </>
  );
};
export default App;
