import React from 'react';
import styled from 'styled-components';

const Eyes = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  top: 2.5rem;
  margin: auto;
  height: 0.5rem;
  width: 7rem;
  background: #4c474b;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: inset 45px 0 #383337;
  -webkit-box-shadow: inset 45px 0 #383337;
  box-shadow: inset 45px 0 #383337;

  &::before {
    content: '';
    position: absolute;
    top: 0.5rem;
    height: 1rem;
    width: 2.3rem;
    background: #e8f0db;
    -moz-border-radius: 0 0 15px 15px;
    -webkit-border-radius: 0;
    border-radius: 0 0 15px 15px;
    -moz-box-shadow: 0 2px 0 2px #4c474b, 0 4px 0 4px #383337;
    -webkit-box-shadow: 0 2px 0 2px #4c474b, 0 4px 0 4px #383337;
    box-shadow: 0 2px 0 2px #4c474b, 0 4px 0 4px #383337;
    left: 8px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0.5rem;
    height: 1rem;
    width: 2.3rem;
    background: #e8f0db;
    -moz-border-radius: 0 0 15px 15px;
    -webkit-border-radius: 0;
    border-radius: 0 0 15px 15px;
    -moz-box-shadow: 0 2px 0 2px #4c474b, 0 4px 0 4px #383337;
    -webkit-box-shadow: 0 2px 0 2px #4c474b, 0 4px 0 4px #383337;
    box-shadow: 0 2px 0 2px #4c474b, 0 4px 0 4px #383337;
    right: 8px;
  }
`;

const App: React.FC = () => {
  return <Eyes />;
};
export default App;
