import styled from 'styled-components';

const Diamond = styled.div`
  width: 0;
  height: 0;
  border: 10rem solid transparent;
  border-bottom: 10rem solid ${(props) => props.color};
  position: relative;
  top: -10rem;

  &::after {
    content: '';
    position: absolute;
    left: -10rem;
    top: 10rem;
    width: 0;
    height: 0;
    border: 10rem solid transparent;
    border-top: 10rem solid ${(props) => props.color};
  }
`;

export default Diamond;
