import React, { useContext, useEffect } from 'react';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import {
  FlexDiv,
  TransBtn,
  BtnImg,
  ColorTxt,
  MonitorView,
  RightBtnDiv,
} from 'components/EtcStyles';
import Background from 'assets/images/background.jpg';
import O from 'assets/images/O.png';
import X from 'assets/images/X.png';
import MonitorLeg from 'assets/images/monitor2.png';
import BacteriaCombination from 'components/BacteriaCombination';
import AppContext from 'App/context';
import {
  PatternFirstType,
  generateBacteria,
  NoBacteriaThings,
} from 'App/datas';
import Button from 'components/Button';
import usePrevious from 'hooks/usePrevious';
import RenderNoBacteria from 'components/RenderNoBacteria';

const getLevel = (gage: number) => {
  let level = 0;
  if (gage >= 50) {
    level = 5;
  } else if (gage >= 25) {
    level = 4;
  } else if (gage >= 20) {
    level = 3;
  } else if (gage >= 10) {
    level = 2;
  } else if (gage >= 5) {
    level = 1;
  }
  return level;
};

const generateSomething = () => {
  if (Math.floor(Math.random() * 100) > 50) {
    return [
      NoBacteriaThings.Dust,
      NoBacteriaThings.Hair,
      NoBacteriaThings.Plant,
    ][Math.floor(Math.random() * 3)];
  }
  return 'bacteria';
};

const Page: React.FC = () => {
  const {
    state: { modelsFirst },
    goToNext,
    learnBacteria,
    learnNoBacteria,
  } = useContext(AppContext);

  const gage =
    modelsFirst.bacteria.learnCount + modelsFirst.noBacteria.learnCount;
  const level = getLevel(gage);
  const prevLevel = usePrevious<number>(level) || 0;
  const param = generateSomething();

  useEffect(() => {
    if (level > prevLevel) {
      // eslint-disable-next-line no-alert
      window.alert(`Level Up ${level}`);
    }
  }, [level, prevLevel]);

  const handleChange = (learn: (_: keyof PatternFirstType) => void) => () => {
    if (gage < 50) {
      learn(param as keyof PatternFirstType);
    }
  };
  const handleChangeO = handleChange(learnBacteria);
  const handleChangeX = handleChange(learnNoBacteria);

  console.log(modelsFirst);

  return (
    <BasePage>
      <Container style={{ backgroundImage: `url(${Background})` }}>
        <DivBase>
          <ContentTitle>AI 1차 학습</ContentTitle>
          <ExplainBox>
            AI는 처음에 공기 중 물체가 세균인지, 아닌지 구분할 수 없어.
            <br /> 우리가 AI에게 알려준다면 우리가 알려준대로 사물을 파악하게
            될거야! 이제 AI에게 세균을 알려주자!
          </ExplainBox>
          <Content style={{ height: 'calc(100% - 2rem)' }}>
            <FlexDiv>
              <TransBtn onClick={handleChangeO}>
                <BtnImg src={O} alt="" />
                <ColorTxt color="#22a415">세균</ColorTxt>
              </TransBtn>

              <div>
                <div>Battery: {gage} / 50</div>
                <MonitorView>
                  {param === 'bacteria' ? (
                    <BacteriaCombination type={generateBacteria()} />
                  ) : (
                    <RenderNoBacteria type={param} />
                  )}
                </MonitorView>
                <img src={MonitorLeg} alt="" />
              </div>

              <TransBtn
                onClick={handleChangeX}
                style={{ marginBottom: '7.8rem' }}
              >
                <BtnImg src={X} alt="" />
                <ColorTxt color="#bf2d2d">세균아님</ColorTxt>
              </TransBtn>
            </FlexDiv>
          </Content>
        </DivBase>
        <RightBtnDiv>
          <Button
            onClick={() => {
              goToNext();
            }}
            disabled={level < 1}
          >
            학습완료
          </Button>
        </RightBtnDiv>
      </Container>
    </BasePage>
  );
};

export default Page;
