import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  ExplainBox,
  Content,
} from 'components/BaseStyles';
import { BoldTitle, RestartBtn } from 'components/EtcStyles';
import ButtonGroup from 'components/ButtonGroup';
import Modal from '@material-ui/core/Modal';
import { generateModelFirstLabel, PatternFirstType } from 'App/datas';
import AppContext, { ModelsFirstType } from 'App/context';
import {
  TableContainer,
  Paper,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from '@material-ui/core';

const HelpBox = styled.div`
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  background: #ececec;
  padding: 1rem;
  border-radius: 1rem;
  max-width: 25rem;
  z-index: 9;
  box-shadow: 5px 5px 7px #c4c4c4;
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1rem;
  border: 1px solid lightgray;
`;

const Cell = styled.div`
  width: 6vw;
  height: 6vw;
  border: 1px solid lightseagreen;

  &:hover {
    background: lightgray;
  }
`;

const ModalBody = styled.div`
  position: absolute;
  width: 30vw;
  height: fit-content;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  color: white;
  text-align: center;
`;

const defaultState: {
  open: boolean;
  target: keyof PatternFirstType | null;
} = { open: false, target: null };

const generateFullData = (
  predict: (param: keyof PatternFirstType) => keyof ModelsFirstType | null,
) => {
  const fullData: {
    bacteria: string[];
    noBacteria: string[];
  } = {
    bacteria: [],
    noBacteria: [],
  };
  while (fullData.bacteria.length < 20 || fullData.noBacteria.length < 20) {
    const label = generateModelFirstLabel();
    if (predict(label) === 'bacteria' && fullData.bacteria.length < 20) {
      fullData.bacteria.push(label);
    } else if (fullData.noBacteria.length < 20) {
      fullData.noBacteria.push(label);
    }
  }
  return fullData;
};

const Page: React.FC = () => {
  const {
    state: { modelsFirst },
    predictFirst,
    goToPrev2,
  } = useContext(AppContext);
  const [state, setState] = useState(defaultState);
  const [datas] = useState(generateFullData(predictFirst));
  const handleClose = () => setState(defaultState);
  const handleClick = (target: keyof PatternFirstType) => () =>
    setState({
      open: true,
      target,
    });

  return (
    <BasePage>
      <Container style={{ position: 'relative' }}>
        <DivBase>
          <ContentTitle>AI 검사결과</ContentTitle>
          <ExplainBox>
            학습 내용에 따라 ai가 세균이라고 구분한 사물은 다음과 같아. 내가
            학습 시킨 AI는 세균을 잘 구분했을까?
          </ExplainBox>
          <Content>
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}
            >
              <div>
                <span>세균</span>
                <Grid>
                  {datas.bacteria.map((data) => (
                    <Cell onClick={handleClick(data as keyof PatternFirstType)}>
                      {data}
                    </Cell>
                  ))}
                </Grid>
              </div>
              <div>
                <span>세균아님</span>
                <Grid>
                  {datas.noBacteria.map((data) => (
                    <Cell onClick={handleClick(data as keyof PatternFirstType)}>
                      {data}
                    </Cell>
                  ))}
                </Grid>
              </div>
            </div>
          </Content>
          <Modal
            open={state.open}
            onClose={handleClose}
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
          >
            <ModalBody>
              <h1>수치 비교</h1>
              <TableContainer component={Paper}>
                <Table aria-label="simple table">
                  <TableHead>
                    <TableRow>
                      <TableCell />
                      <TableCell>세균</TableCell>
                      <TableCell>세균아님</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {(['bacteria', 'dust', 'hair', 'plant'] as Array<
                      keyof PatternFirstType
                    >).map((key) => (
                      <TableRow>
                        <TableCell
                          style={{
                            color: key === state.target ? 'red' : 'initial',
                          }}
                        >
                          {key}
                        </TableCell>
                        <TableCell>
                          {modelsFirst.bacteria.pattern[key]}
                        </TableCell>
                        <TableCell>
                          {modelsFirst.noBacteria.pattern[key]}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </ModalBody>
          </Modal>
        </DivBase>
        <HelpBox>
          <BoldTitle>AI가 세균을 잘 구분 못하나요? </BoldTitle>AI는 내가
          가르쳐준 내용을 그대로 학습해요. <br />
          학습 데이터를 더 제공할 수록 ai가 더 많이 배웁니다.
          <br /> ai를 다시 학습시켜볼까요?
          <br />
          <RestartBtn
            onClick={() => {
              goToPrev2();
            }}
          >
            다시 학습하기
          </RestartBtn>
        </HelpBox>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default Page;
