import React, { useContext, useEffect } from 'react';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ButtonGroup from 'components/ButtonGroup';
import AppContext, { ModelsSecondType } from 'App/context';
import BacteriaCombination from 'components/BacteriaCombination';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const exampleSize = 50;
const showSize = 3;
const getLabel = (result: keyof ModelsSecondType | null) => {
  switch (result) {
    case 'noxious':
      return '유해균';
    case 'innoxious':
      return '무해균';
    default:
      return '-';
  }
};

const Page: React.FC = () => {
  const {
    state: { bacterias },
    generateBacterias,
    predictSecond,
  } = useContext(AppContext);

  useEffect(() => {
    if (bacterias.length === 0) {
      generateBacterias(exampleSize);
    }
  }, [bacterias, generateBacterias]);

  if (bacterias.length === 0) {
    return null;
  }

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI 성능검사</ContentTitle>
          <ExplainBox>
            학습 데이터를 활용해 AI가 유해균과 무해균을 인식하도록 훈련했어!
            <br />
            이제 AI가 학습 내용에 따라 유해균을 파악해서 제거하기 시작해.
          </ExplainBox>
          <Content>
            <Slider
              autoplay
              infinite
              slidesToShow={showSize}
              speed={500}
              autoplaySpeed={1500}
              centerPadding="300px"
              dots={false}
              arrows={false}
            >
              {bacterias.map((bacteria) => (
                <div key={JSON.stringify(bacteria)}>
                  <BacteriaCombination type={bacteria} />
                  <div>{getLabel(predictSecond(bacteria))}</div>
                </div>
              ))}
            </Slider>
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default Page;
