import React, { useContext, useState } from 'react';
import AppContext, { ModelsFirstType } from 'App/context';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import {
  RelativeDiv,
  RobotImg,
  Trapezoid,
  OXBox,
  Current,
  Next,
  FirstBoxDiv,
  OtherBoxDiv,
  RightBtnDiv,
  RightBtn,
} from 'components/EtcStyles';
import Robot from 'assets/images/standing_robot.png';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import BacteriaCombination from 'components/BacteriaCombination';
import {
  generateBacteria,
  generateModelFirstLabel,
  PatternFirstType,
} from 'App/datas';

const exampleSize = 50;
const showSize = 3;
const getLabel = (result: keyof ModelsFirstType | null) => {
  switch (result) {
    case 'bacteria':
      return '세균';
    case 'noBacteria':
      return '세균아님';
    default:
      return '-';
  }
};

const Page: React.FC = () => {
  const { goToNext, predictFirst } = useContext(AppContext);

  const [somethings] = useState<(keyof PatternFirstType)[]>([
    ...Array.from(Array(50)).map(() => generateModelFirstLabel()),
  ]);

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI 성능검사</ContentTitle>
          <ExplainBox>
            이제 AI가 배운 내용에 따라 세균과 사물을 분석하고 구할 수 있어!
            <br />
            과연 내가 만든 AI는 공기 중 세균을 잘 파악할까?
          </ExplainBox>
          <Content>
            <RelativeDiv>
              <Trapezoid />
              <div>
                <RobotImg src={Robot} />
              </div>
              <div
                style={{
                  position: 'absolute',
                  bottom: '7rem',
                  left: '20rem',
                  width: '45rem',
                  height: '15rem',
                  // background: 'pink',
                }}
              >
                <Slider
                  autoplay
                  infinite
                  slidesToShow={3}
                  speed={500}
                  autoplaySpeed={1500}
                  centerPadding="300px"
                  dots={false}
                  arrows={false}
                >
                  {somethings.map((something) => (
                    <div>
                      {something === 'bacteria' ? (
                        <BacteriaCombination type={generateBacteria()} />
                      ) : (
                        <>{something}</>
                      )}
                      <div>{getLabel(predictFirst(something))}</div>
                    </div>
                  ))}
                </Slider>
              </div>
            </RelativeDiv>
          </Content>
        </DivBase>
        <RightBtnDiv>
          <RightBtn
            onClick={() => {
              goToNext();
            }}
          >
            결과보기
          </RightBtn>
        </RightBtnDiv>
      </Container>
    </BasePage>
  );
};

export default Page;
