import React from 'react';
import styled from 'styled-components';
import { NoBacteriaThings } from 'App/datas';
import { scaleUp } from 'styles/animations';

import Dust0 from 'assets/images/dust1.jpg';
import Dust1 from 'assets/images/dust2.jpg';
import Dust2 from 'assets/images/dust3.jpg';

import Hair0 from 'assets/images/hair1.jpg';
import Hair1 from 'assets/images/hair2.jpg';
import Hair2 from 'assets/images/hair3.jpg';

import Plant0 from 'assets/images/plant1.jpg';
import Plant1 from 'assets/images/plant2.jpg';
import Plant2 from 'assets/images/plant3.jpg';

const SometingDiv = styled.div`
  width: 20rem;
  height: 20rem;
`;

const somethingRender = (type: number) => {
  switch (type) {
    case NoBacteriaThings.Dust:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <img src={Dust0} alt="" />;
        case 1:
          return <img src={Dust1} alt="" />;
        case 2:
          return <img src={Dust2} alt="" />;
        default:
          break;
      }
      return <></>;
    default:
      break;

    case NoBacteriaThings.Hair:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <img src={Hair0} alt="" />;
        case 1:
          return <img src={Hair1} alt="" />;
        case 2:
          return <img src={Hair2} alt="" />;
        default:
          break;
      }
      return <></>;

    case NoBacteriaThings.Plant:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <img src={Plant0} alt="" />;
        case 1:
          return <img src={Plant1} alt="" />;
        case 2:
          return <img src={Plant2} alt="" />;
        default:
          break;
      }
      return <></>;
  }
  return <></>;
};

type Props = {
  type: number;
  scale?: number;
};

const RenderSomething: React.FC<Props> = ({ type, scale = 1 }) => {
  return (
    <SometingDiv style={{ transform: `scale(${scale})` }}>
      {somethingRender(type)}
      {/* {type} */}
    </SometingDiv>
  );
};
export default RenderSomething;
