import React from 'react';
import styled from 'styled-components';

const Face = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 10rem;
  height: 10rem;

  &::before,
  &::after {
    position: absolute;
    content: '';

    top: 0.5rem;
    left: 7.7rem;
    z-index: 5;
    width: 1.8rem;
    height: 1.4rem;
    border-radius: 50% / 40%;
    border-right: 5px solid #000;
    border-bottom: 5px solid #000;
    border-left: 5px solid transparent;
    border-top: 5px solid transparent;
    box-shadow: 0px 0px #000;
  }
  &::after {
    left: 9.7rem;
    transform: rotateY(180deg);
  }
`;

const Features = styled.div`
  position: absolute;
  content: '';

  &::after {
    position: absolute;
    content: '';
    left: 9.1rem;
    top: -0.5rem;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #000;
    border-radius: 50%;
    box-shadow: 2px 0 #000, 1px 1px #000;
  }
`;

const App: React.FC = () => {
  return (
    <>
      <Face />
      <Features />
    </>
  );
};
export default App;
