import styled from 'styled-components';
import { Bounce } from 'styles/animations';
import { Content } from 'components/BaseStyles';

export const FlexDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
`;

export const TransBtn = styled.button`
  outline: 0;
  border: 0;
  cursor: pointer;
  background: none;
  max-width: 180px;
  max-height: 180px;
  padding: 0;
  margin: 0 2rem 7rem 2rem;
`;

export const BtnImg = styled.img`
  width: 100%;
`;

export const ColorTxt = styled.div`
  color: ${(props) => props.color};
  font-size: 1.8rem;
  font-weight: 800;
  font-family: 'Yspot';
`;

export const MonitorView = styled.div`
  width: 42rem;
  height: 28rem;
  background-color: #fff;
  border: 20px solid #333;
  border-radius: 1rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const CustomBtn = styled.button`
  padding: 1rem 3rem;
  color: #fff;
  background-color: #1696e5;
  outline: 0;
  border: 0;
  font-size: 1.8rem;
  font-family: 'Yspot', sans-serif;
  border-radius: 0.5rem;
  cursor: pointer;
`;

export const CustomBtnDiv = styled.div`
  position: absolute;
  bottom: 2rem;
`;

export const RightBtnDiv = styled(CustomBtnDiv)`
  bottom: 3rem;
  right: 10rem;
`;
export const RightBtn = styled(CustomBtn)`
  padding: 0.2rem 2rem;
`;

export const BlackBox = styled.div`
  margin-left: 3rem;
  background-color: #41474f;
  border-radius: 1.5rem;
  width: 25rem;
  padding: 1.5rem;
  color: #fff;
  font-weight: 100;
  font-family: 'Score';
  font-size: 1.1rem;
`;

export const BlackBoxBtn = styled.div`
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  background-color: ${(props) => props.color || 'white'};
  margin: 0 0.5rem 1rem 0;
`;

export const RelativeDiv = styled.div`
  display: flex;
  height: 100%;
  position: relative;
`;

export const RobotImg = styled.img`
  width: 15rem;
  position: absolute;
  bottom: 7rem;
  animation: ${Bounce} 0.5s infinite alternate;
`;

export const Trapezoid = styled.div`
  position: absolute;
  right: -2rem;
  bottom: 4.7rem;
  width: 100%;
  height: 50%;
  background: linear-gradient(to right, #ececec, #fff);
  transform: perspective(20px) rotateY(-1deg);
  margin: 50px;
`;

export const FirstBoxDiv = styled.div`
  position: absolute;
  width: 15rem;
  height: 18rem;
  top: 8rem;
  left: 22rem;
  display: flex;
  flex-direction: column;
`;

export const OtherBoxDiv = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: start;
  width: 45rem;
  height: 15rem;
  top: 8rem;
  left: 40rem;
`;

export const OXBox = styled.div`
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  font-size: 2rem;
  font-family: 'Yj';
`;

export const Current = styled.div`
  width: 15rem;
  height: 15rem;
  border: 8px solid #757575;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const Next = styled(Current)`
  border: 0;
  margin-right: 1rem;
`;

export const CustomContent = styled(Content)`
  margin-top: 3rem;
  display: flex;
`;

export const RangeDiv = styled.div`
  width: 50%;
  border-radius: 2rem;
  margin: 0 1rem;
  :first-child {
    border: 0.8rem solid #22a415;
  }
  :last-child {
    border: 0.8rem solid #bf2d2d;
  }
`;
export const Title = styled.div`
  background-color: #22a415;
  color: #fff;
  font-size: 1.5rem;
  padding: 1rem;
  width: 8rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.8rem 0 0 0.8rem;
  border-radius: 1rem;
`;
export const Title2 = styled(Title)`
  background-color: #bf2d2d;
`;
export const BoldTitle = styled.div`
  font-weight: bold;
  margin-bottom: 1rem;
`;
export const RestartBtn = styled.button`
  border: 1px solid #999;
  border-radius: 0.7rem;
  background-color: #fff;
  cursor: pointer;
  font-family: 'Yspot';
  color: #717578;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
`;
