import React from 'react';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ButtonGroup from 'components/ButtonGroup';

const Page: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI는 기계학습의 결과물</ContentTitle>
          <ExplainBox>
            오늘 우리가 체험한 것은 기계학습 기법 중 하나인 지도학습이라고 해!
            <br />
            체험해본대로 지도학습을 통해 만들어진 AI는 사람이 가르친 것만 행동할
            수 있어. <br /> 그렇기에 사람인, 우리의 역할이 중요해! 우리의 판단을
            배우고 대신 해서 행동하는게 인공지능이야. 알겠지?
          </ExplainBox>
          <Content>
            실생활에서 인공지능에게 가르칠 수 있는 게 어떤 것이 있는지 친구들과
            이야기 나눠보자
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default Page;
