import React from 'react';
import styled from 'styled-components';

const WhiteLeft = styled.div`
  position: absolute;
  top: 2.5rem;
  left: 7rem;
  background: #fff;
  width: 2rem;
  height: 2.5rem;
  border-radius: 100%;
`;
const WhiteRight = styled(WhiteLeft)`
  transform: translateX(70px);
`;

const BlackLeft = styled.div`
  position: absolute;
  top: 0.5rem;
  left: 0.7rem;
  background: #000;
  width: 1.2rem;
  height: 1.4rem;
  border-radius: 100%;
`;
const BlackRight = styled(BlackLeft)`
  transform: translateX(-9px);
`;

const App: React.FC = () => {
  return (
    <>
      <WhiteLeft>
        <BlackLeft />
      </WhiteLeft>
      <WhiteRight>
        <BlackRight />
      </WhiteRight>
    </>
  );
};
export default App;
