import React from 'react';
import styled, { keyframes } from 'styled-components';

const Blink = keyframes`
  100% {transform: rotateX(180deg);}
`;
const Eyebrows = keyframes`
  100% {top: 2rem;}
`;

const Eyebrow1 = styled.div`
  width: 3rem;
  height: 0.7rem;
  border-radius: 1rem;
  background-color: black;
  float: left;
  position: absolute;
  top: 1.5rem;
  left: 6rem;
  transform: rotate(25deg);
  animation: ${Eyebrows} 1s infinite alternate;
`;
const Eyebrow2 = styled.div`
  width: 3rem;
  height: 0.7rem;
  border-radius: 1rem;
  background-color: black;
  margin-left: 35px;
  float: left;
  position: absolute;
  top: 1.5rem;
  right: 6rem;
  transform: rotate(-25deg);
  animation: ${Eyebrows} 1s infinite alternate;
`;
const Eye = styled.div`
  float: left;
  height: 40px;
  width: 20px;
  background: black;
  border-radius: 50%;
  margin-top: 3rem;
  margin-left: 7rem;
  animation: ${Blink} 1s cubic-bezier(1, 0.01, 1, -0.08) alternate infinite both;
  :last-child {
    margin-left: 3.5rem;
  }
`;

const App: React.FC = () => {
  return (
    <>
      <Eyebrow1 />
      <Eyebrow2 />
      <Eye />
      <Eye />
    </>
  );
};
export default App;
