import React from 'react';
import styled from 'styled-components';

const Eye = styled.div`
  position: absolute;
  top: 3rem;
  left: 6.5rem;
  width: 1.8rem;
  height: 1.8rem;
  background: #fff;
  border-radius: 100%;
  border: 4px solid #333;

  &::before {
    position: absolute;
    content: '';
    top: -0.2rem;
    left: 5rem;
    width: 1.4rem;
    height: 1.4rem;
    background: #fff;
    border-radius: 100%;
    border: 4px solid #333;
  }
`;

const App: React.FC = () => {
  return <Eye />;
};
export default App;
