import React, { useContext } from 'react';
import AppContext from 'App/context';
import styled from 'styled-components';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import {
  CustomBtn,
  CustomBtnDiv,
  BlackBox,
  BlackBoxBtn,
} from 'components/EtcStyles';
import Robot from 'assets/images/circle_robot.png';

const Div = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
`;

const FlexDiv = styled.div`
  display: flex;
`;

const Page: React.FC = () => {
  const { goToNext } = useContext(AppContext);
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>체험으로 배우는 AI 학습과정</ContentTitle>
          <ExplainBox>
            사람처럼 컴퓨터가 결정을 내릴 수 있는 이유는 인공지능이 있기
            때문이야. <br />
            인공지능(AI)는 기계학습(머신러닝)을 통해 만들어지는데, 함께 AI를
            만들면서 이 과정을 체험해보자!
          </ExplainBox>
          <Content>
            <Div>
              <div>
                <img src={Robot} alt="" style={{ width: '15rem' }} />
              </div>
              <BlackBox>
                <FlexDiv>
                  <BlackBoxBtn color="#f0564c" />
                  <BlackBoxBtn color="#f9bd3a" />
                  <BlackBoxBtn color="#67d141" />
                </FlexDiv>
                <div style={{ textAlign: 'initial' }}>
                  오늘 우리는 기계학습(머신러닝)을 통해 세균을 탐지하여 유해균을
                  제거하는 공기청정 AI 로봇을 만들어볼거에요.
                </div>
              </BlackBox>
            </Div>
          </Content>
        </DivBase>
        <CustomBtnDiv>
          <CustomBtn
            onClick={() => {
              goToNext();
            }}
          >
            알아보기
          </CustomBtn>
        </CustomBtnDiv>
      </Container>
    </BasePage>
  );
};

export default Page;
