const mouseRender = (mouse: number) => {
  switch (mouse) {
    case Mouse.Good:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <PuppyMouse />;
        case 1:
          return <TongueSmileMouse />;
        case 2:
          return <SmileMouse />;
        default:
          break;
      }
      return <></>;
    default:
      break;

    case Mouse.Soso:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <SquareTeethMouse />;
        case 1:
          return <SurprisedMouse />;
        case 2:
          return <NormalMouse />;
        default:
          break;
      }
      return <></>;

    case Mouse.Bad:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <BloodMouse />;
        case 1:
          return <ThreeTeethMouse />;
        case 2:
          return <SalivaMouse />;
        default:
          break;
      }
      return <></>;
  }
  return <></>;
};
const BodyDiv = styled.div`
  width: 20rem;
  height: 20rem;
  position: relative;
  /* animation: ${scaleUp} 0.7s infinite alternate; */
`;
const EyeDiv = styled.div`
  position: absolute;
  top: 15%;
  width: 100%;
  height: 5.5rem;
`;
const MouseDiv = styled.div`
  width: 100%;
  height: 25%;
  position: absolute;
  top: 8.5rem;
`;

type Props = {
  type: Bacteria | null;
  scale?: number;
};

const BacteriaCombination: React.FC<Props> = ({ type, scale = 1 }) => {
  if (!type) {
    return <div>-</div>;
  }
  const { body, eyes, mouse, color } = type;

  return (
    <BodyDiv style={{ transform: `scale(${scale})` }}>
      <Bodies body={body} color={color} />
      <EyeDiv>{eyesRender(eyes)}</EyeDiv>
      <MouseDiv>{mouseRender(mouse)}</MouseDiv>
    </BodyDiv>
  );
};
export default BacteriaCombination;
