import React from 'react';
import styled from 'styled-components';

const Mouse = styled.div`
  z-index: 1;
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5rem;
  height: 0.5rem;
  background: #111;
`;

const TeethLeft = styled.div`
  position: absolute;
  top: 1rem;
  left: 42.5%;
  width: 1.2rem;
  height: 1.2rem;
  background: #fff;
  border-radius: 0.3rem;
  border: 4px solid #111;
`;
const TeethRight = styled(TeethLeft)`
  transform: translateX(1.8rem);
`;

const App: React.FC = () => {
  return (
    <>
      <Mouse />
      <TeethLeft />
      <TeethRight />
    </>
  );
};
export default App;
