import styled from 'styled-components';

const Cloud = styled.div`
  background: ${(props) => props.color};
  width: 12rem;
  height: 12rem;
  margin-left: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &::before {
    border-radius: 50%;
    box-shadow: 12rem 0 0 0 ${(props) => props.color},
      11.5rem -2.5rem 0 -0.2rem ${(props) => props.color},
      10em -5rem 0 -0.2rem ${(props) => props.color},
      11rem -8rem 0 0.5rem ${(props) => props.color},
      8rem -9.5rem 0 -0.2rem ${(props) => props.color},
      5rem -10rem 0 0 ${(props) => props.color},
      1.5rem -8.5rem 0 0.3rem ${(props) => props.color},
      0.5rem -6rem 0 -0.2rem ${(props) => props.color},
      1rem -3rem 0 0.2rem ${(props) => props.color},
      2rem 1rem 0 0.2rem ${(props) => props.color},
      5rem 1.5rem 0 0.2rem ${(props) => props.color},
      8rem 2rem 0 -0.1rem ${(props) => props.color},
      10.5rem 1.5rem 0 -0.1rem ${(props) => props.color};
    content: '';
    position: absolute;
    width: 4rem;
    height: 4rem;
    left: -2rem;
    bottom: 0;
    background: ${(props) => props.color};
  }
`;

export default Cloud;
