import React from 'react';
import styled, { keyframes } from 'styled-components';

const Wink = keyframes`
	15% {
  width: 1.5rem;
  height: 0.2rem;
  transform: rotate(2deg);
	}
`;
const WinkBrowRight = keyframes`
  15% {
  -webkit-transform: rotate(-10deg);
  transform: rotate(-10deg);
  }
`;
const WinkBrowLeft = keyframes`
  15% {
    top: 3rem;
  }
`;
const EyebrowLeft = styled.div`
  position: absolute;
  top: 3.2rem;
  width: 2rem;
  height: 0.5rem;
  border-radius: 1rem;
  background: #000;
  transition: all 0.2s;
  left: 6rem;
  animation: ${WinkBrowLeft} 1.2s infinite;
`;
const EyebrowRight = styled(EyebrowLeft)`
  left: 11.8rem;
  animation: ${WinkBrowRight} 1.2s infinite;
`;
const EyeLeft = styled.div`
  position: absolute;
  top: 4.2rem;
  left: 6.5rem;
  border-radius: 100%;
  height: 1.2rem;
  width: 1.2rem;
  background-color: #111;
`;
const EyeRight = styled(EyeLeft)`
  left: 12rem;
  animation: ${Wink} 1.2s infinite;
`;

const App: React.FC = () => {
  return (
    <>
      <EyebrowLeft />
      <EyeLeft />
      <EyebrowRight />
      <EyeRight />
    </>
  );
};
export default App;
