import React from 'react';
import styled, { keyframes } from 'styled-components';

const Blooddrop = keyframes`
  0% { opacity: 0; }
  100% { transform: translateY(50px) rotate(-45deg); opacity: 1;}
`;

const Mouse = styled.div`
  height: 3rem;
  width: 7rem;
  background: black;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 20px 20px 50% 50%;
`;
const ToothLeft = styled.div`
  position: absolute;
  left: 1.5rem;
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 30px solid white;
  float: left;
`;
const ToothRight = styled.div`
  position: absolute;
  left: 2.7rem;
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 30px solid white;
  float: left;
  margin-left: 20px;
`;
const Blood = styled.div`
  position: absolute;
  top: 1rem;
  right: 7.5rem;
  width: 1.8rem;
  height: 1.8rem;
  border: 0 solid rgba(0, 0, 0, 1);
  -webkit-border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  transform: rotate(-45deg);
  background: #f53057;
  animation: ${Blooddrop} 1.5s infinite;
`;

const App: React.FC = () => {
  return (
    <>
      <Mouse>
        <ToothLeft />
        <ToothRight />
      </Mouse>
      <Blood />
    </>
  );
};
export default App;
