import React from 'react';
import styled from 'styled-components';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ButtonGroup from 'components/ButtonGroup';
import Newsfeed from 'assets/images/newsfeed.jpg';
import Car from 'assets/images/car.jpg';

const ImgDiv = styled.div`
  width: 50%;
  margin-left: 1rem;
`;
const Img = styled.img`
  width: 100%;
  height: 80%;
`;
const ImgTxt = styled.div`
  margin: 1.2rem 0 1rem 0;
  color: #999;
  font-weight: 800;
`;

const General: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI는 일상 곳곳에</ContentTitle>
          <ExplainBox>
            컴퓨터들이 결정을 내리고, 그러한 결정에 우리의 일상이 영향받고 있어.
            <br />
            인터넷 검색결과를 보거나 뉴스 피드를 스크롤해서 볼 때, 우리가 무엇을
            볼지 컴퓨터가 결정하지.
            <br /> 어떻게 컴퓨터는 이런 결정을 내리는 걸까?
          </ExplainBox>
          <Content style={{ display: 'flex' }}>
            <ImgDiv>
              <ImgTxt>SNS</ImgTxt>
              <Img src={Newsfeed} alt="" />
            </ImgDiv>
            <ImgDiv>
              <ImgTxt>자율주행 자동차</ImgTxt>
              <Img src={Car} alt="" />
            </ImgDiv>
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default General;
